import React, { Component } from 'react';
import * as d3 from 'd3';
// import '../styles/test3.sass';

class HorizontalBarChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: null
    };
  }

  componentDidMount() {
    if(this.props.data) {
      this.setState({
        data: this.props.data
      });
      this.drawHorizontalBarChart(this.props.data);
    }
  }

  updateChart(data){
    d3.select(this.refs.canvas).selectAll('g').remove();
    this.drawHorizontalBarChart(data);
  }

  drawHorizontalBarChart(data) {
    data = data.sort(function (a, b) {
      return d3.ascending(a.value, b.value);
    }).slice(Math.max(data.length - 10, 0));

    // set up svg using margin conventions - we'll need plenty of room on the left for labels
    var margin = {
      top: 15,
      right: 100,
      bottom: 15,
      left: 100
    };

    var width = 600 - margin.left - margin.right,
      height = 500 - margin.top - margin.bottom;

    d3.select(this.refs.canvas)
      .append('rect')
      .attr('width', '100%')
      .attr('height', '100%')
      .attr('fill', '#EBEBED')
      .attr('rx', '15');
    var svg = d3.select(this.refs.canvas)
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')');

    const t = d3.transition()
      .duration(750);

    var x = d3.scaleLinear()
      .range([0, width])
      .domain([0, d3.max(data, d => d.value)]);

    var y = d3.scaleBand()
      .range([height, 0], 0.1)
      .domain(data.map(d => d.name))
      .padding(0.1);

    // make y axis to show bar names
    var yAxis = d3.axisLeft()
      .scale(y)
      // no tick marks
      .tickSize(0);
      // .orient("left");

    var gy = svg.append('g')
      .attr('class', 'y axis')
      .call(yAxis);

    var bars = svg.selectAll('.horizontal-bar')
      .data(data)
      .enter()
      .append('g')
      .attr('class', 'horizontal-bar');

    // append rects
    bars.append('rect')
      .attr('y', d => y(d.name))
      .attr('height', y.bandwidth())
      .attr('x', 0)
      .transition(t)
      .attr('width', d => x(d.value));

    // add a value label to the right of each bar
    bars.append('text')
      .attr('class', 'd3-label')
      // y position of the label is halfway down the bar
      .attr('y', d => y(d.name) + y.bandwidth() / 2 + 4)
      // x position is 3 pixels to the right of the bar
      .transition(t)
      .attr('x', d => x(d.value) + 3)
      .text(d => d.value);

  }

  componentDidUpdate(prevProps, prevState) {
    this.setState({
      data: this.props.data
    });
    this.updateChart(this.props.data);
  }

  shouldComponentUpdate(nextProps, nextState) {
    // console.log('shouldComponentUpdate nextProps, nextState', nextProps, nextState, this.props, this.state);
    const prevSum = this.props.data.reduce((prev, curr) => prev + curr.value, 0);
    const nextSum = nextProps.data.reduce((prev, curr) => prev + curr.value, 0);
    // console.log('prevsum next sum', prevSum, nextSum);
    if(prevSum === nextSum) {
      return false;
    }
    return true;
  }

  render() {
    return (
      <div key={this.props.title}>
        <h5 className="title is-5">{ this.props.title }</h5>
        <svg ref="canvas"></svg>
      </div>);
  }
}
export default HorizontalBarChart;