import Router from 'next/router';
import { withRouter } from 'next/router';
import React, { Component } from 'react';

function nav({ router }) {
  return (
    <nav className="navbar navbar-a" role="navigation" aria-label="main navigation">
    <a className={`${router.pathname === '/about' ? 'clickable-active' : ''} navbar-item`} href="/about" >
          About
    </a>
    <a className={`${router.pathname.indexOf('/analyze') !== -1 ? 'clickable-active' : ''} navbar-item`} href="/analyze" >
          Analyze
    </a>
  </nav>
  );
}

export default withRouter(nav);