import React, { Component } from 'react';
import * as d3 from 'd3';
// import '../styles/test3.sass';

class TreeMap extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: null
    };
  }

  componentDidMount() {
    if(this.props.data) {
      this.setState({
        data: this.props.data
      });
      this.graphTreemap(this.props.data);
    }
  }

  graphTreemap(data) {
    // set the dimensions and margins of the graph
    var margin = {top: 5, right: 30, bottom: 5, left: 30},
      width = 650 - margin.left - margin.right,
      height = 500 - margin.top - margin.bottom;

    d3.select(this.refs.svg)
      .append('rect')
      .attr('width', '100%')
      .attr('height', '100%')
      .attr('fill', '#EBEBED')
      .attr('rx', '15');
    // append the svg object to the body of the page
    var svg = d3.select(this.refs.svg)
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left},${-15})`);

    // Give the data to this cluster layout:
    var root = d3.hierarchy({
      children: data.filter(x => x.count > 50).map(c => ({children: [c]}))
    }).sum(d => +d.count); // Here the size of each leave is given in the 'value' field in input data

      // Then d3.treemap computes the position of each element of the hierarchy
    d3.treemap()
      .size([width, height])
      .paddingTop(28)
      .paddingRight(7)
      .paddingInner(3)(root);

    var mousemove = function(d) {
      var xPosition = d3.event.pageX + 5;
      var yPosition = d3.event.pageY + 5;

      d3.select('.tree-map-tooltip')
        .style('left', `${xPosition}px`)
        .style('top', `${yPosition}px`);
      d3.select('.tree-map-tooltip-label')
        .text(`${percentage(+d.data.count, 2500)}%`);
      d3.select('.tree-map-tooltip').classed('hidden', false);
    };

    var mouseout = function() {
      d3.select('.tree-map-tooltip').classed('hidden', true);
    };
      // use this information to add rectangles:
    svg
      .selectAll('rect')
      .data(root.leaves())
      .enter()
      .append('rect')
      .attr('x', d => d.x0)
      .attr('y', d => d.y0)
      .attr('width', d => d.x1 - d.x0)
      .attr('height', d => d.y1 - d.y0)
      .style('stroke', 'black')
      .style('fill', d => d.data.color)
      .on('mousemove', mousemove)
      .on('mouseout', mouseout);

    // and to add the text labels

    // and to add the text labels

    // Add title for the 3 groups
    svg
      .selectAll('titles')
      .data(root.descendants().filter(d => d.depth ===1))
      .enter()
      .append('text')
      .attr('x', d => d.x0)
      .attr('y', d => d.y0+21)
      .text(d => d.data.children[0].color)
      .attr('font-size', '16px')
      .attr('fill', d => (d.data.name));

    // });

    function percentage(value, total){
      return (Number(value) * 100) / total;
    }

  }

  render() {
    return (<div key={this.props.title}>
      <h5 className="title is-5">{ this.props.title }</h5>
      <svg ref="svg"></svg>
      <div className="tree-map-tooltip hidden">
        <p><strong className="tree-map-tooltip-label"></strong></p>
      </div>
    </div>);
  }
}
export default TreeMap;