import React, { Component } from 'react';
import * as d3 from 'd3';
class ColorBar extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    if(this.props && this.props.data) {
      this.drawBar(this.props.data);
    } else {
      console.log('no data provided for color bar chart');
      console.log(this.props);
    }
  }
  drawBar(data) {
    const t = d3.transition()
    .duration(750);
    const width = 800;
    const rectWidth = width / data.length;
    const spaceBetween = rectWidth;

    var margin = {top: 20, right: 20, bottom: 20, left: 20},
      svgwidth = 800,
      height = 130;

    var svg = d3.select(this.refs.svg)
      .attr('width', svgwidth + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`);


    var mousemove = function(d) {
      var xPosition = d3.event.pageX + 5;
      var yPosition = d3.event.pageY - 65;

      // console.log(d);
      // this.parentElement.appendChild(this);
      // d3.select(this)
      // .transition(t)
      // .attr('width', '100');
      d3.select('.color-bar-tooltip')
        .style('left', `${xPosition}px`)
        .style('top', `${yPosition}px`)
        .style('background-color', d);
      d3.select('.color-bar-tooltip-label')
        .text(d);
        // .style('margin-top', '40px')
        // .style('background-color', 'white');
      d3.select('.color-bar-tooltip').classed('hidden', false);
    };

    var mouseout = function() {
      // d3.select(this).attr('width', rectWidth);
      d3.select('.color-bar-tooltip').classed('hidden', true);
    };

    var svgX = 0;
    svg
      .selectAll('rect')
      .data(data)
      .enter()
      .append('rect')
      .attr('x', d => {
        const res = Math.round(svgX * 1000) / 1000;
        svgX += spaceBetween;
        return res;
      })
      .attr('y', () => 0)
      .attr('width', () => rectWidth)
      .attr('height', () => height)
      .style('fill', d => {
        return d;
      })
      .on('mousemove', mousemove)
      .on('mouseout', mouseout);
  }
  render() {
    return <div>
      <h2 className="title is-4">{ this.props.title }</h2>
      {
        this.props.data ? <span>A total of <b>{this.props.data.length}</b> colors were found among all the stylesheet files referenced on this website.</span>
          : null
      }
      <svg ref="svg"></svg>
      <div className="color-bar-tooltip hidden">
        <p><strong className="color-bar-tooltip-label"></strong></p>
      </div>
    </div>;
  }
}
export default ColorBar;